.profile {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
    background-color: #ffff;
    margin: 2rem;
}
.profile .heading {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    margin: 2rem;
    width: 75%;
}
.profile .heading p:first-of-type {
    font-size: 35px;
    font-weight: bold;
    padding: 0;
    margin: 0;
}
.profile .heading .p-second {
    font-size: 25px;
    font-weight: 200;
    color: #c6c2c2;
    padding: 0;
    margin: 0;
}
.profile .heading .heading-img  button {
    border: 3px solid #7b7777;
    padding: 10px;
    font-size: 17px;
    cursor: pointer;
    font-weight: 600;
    border-radius: 10px;
}
.profile .heading .heading-img  button:hover {
    border-color: #c6c2c2;
    background-color: #c6c2c2;
    color: #000;
}
.profile .social {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;
    width: 75%;
    margin: 0 2rem 2rem 2rem;
}
.profile .social p:first-of-type {
    font-size: 35px;
    font-weight: bold;
    padding: 0;
    /* margin-bottom:1rem ; */
}
.profile .social .accounts form {
    display: flex;
    flex-direction: column;
    position: relative;
    gap: 1rem;
}
.profile .social .accounts form input {
    width: 300px;
    height: 40px;
    font-size: 20px;
    font-weight: 400;
    color: #000;
    cursor: pointer;
    border-radius: 10px;
    border: 3px solid #c6c2c2;
    padding:0  50px;
    text-align: center;
}
.profile .social .accounts form input ::placeholder {
    color: #c6c2c2;
}
.profile .social .accounts form .icon {
    position: absolute;
}
.profile .social .accounts form .icon.first {
    width: 30px;
    height: 30px;
    top: 10px;
    padding:0  5px;
}
.profile .social .accounts form .icon.second {
    width: 30px;
    height: 30px;
    top: 70px;
    padding:0  5px;
}
@media (max-width:400px){
.profile .social {
    flex-direction: column;
}
.profile .social p:first-of-type {
    font-size: 30px;
}
.profile .social .accounts form input {
    width: 100px;
}
.profile .heading {
    flex-direction: column;
    gap: 2rem;
}
.profile .heading input {
    margin: 1rem 0;
}
}
