.log-in {
    background-image: url('../../assets/Lam-show.webp');
    background-repeat: no-repeat;
    background-size:cover;
    height: 100vh;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 4rem;
    position: relative;
}
.log-in::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    background-color: #00000054;;
    width: 100%;
    height: 100%;
}
.log-in .nav-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 1000;
}
.log-in .img {
    display: flex;
    flex-direction: row;
    gap: 10px;
}
.log-in .img h1 {
    margin: 0;
    padding: 0;
    font-size: 20px;
    font-weight: 600;
}
.log-in  .log-sign {
    display: flex;
    flex-direction: row;
    font-size: 20px;
    font-weight: 600;
    position: relative;
}
.log-in  .log-sign .log {
    cursor: pointer;
    margin: 5px;
    border: 1px solid #000;
    background-color: white;
    padding: 1px;
}
.log-in  .log-sign .sign {
    cursor: pointer;
    margin: 5px;
    border: 1px solid #000;
    background-color: white;
    padding: 1px;
}
.log-in  .log-sign .log:hover::before {
    width: 60px;
}
.log-in  .log-sign .sign:hover::after {
    width: 60px;
}
.log-in .img img {
    width: 25px;
    height: 25px;
}
.log-in .form {
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 100;
}
.log-in .form form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 25px;
    /* background-color: hsla(265, 100%, 92%, 0.342); */
    background-color: hsl(265deg 100% 92% / 62%);
    width: fit-content;
    height: fit-content;
    padding: 3rem;
    border-radius: 10px;
}
.log-in .form form input {
    background-color:#d3ccdd ;
    border: none;
    outline: none;
    width: fit-content;
    padding: 5px;
    border-radius: 10px;
    caret-color: brown;
    padding: 1rem;
    font-size: 17px;
    letter-spacing: 0.5px;
}
.log-in .form form label {
    font-size: 17px;
    font-weight: 500;
}
.log-in .form form button {
    width: fit-content;
    height: fit-content;
    padding: 10px;
    border: none;
    outline: none;
    background-color: #fff;
    color: #000;
    border-radius: 20px;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
}
.log-in .form form button:hover {
    background-color: #b793e8;
}