.setting {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin:3rem 0;
    cursor: pointer;
}
.setting .sign-out {
    position: absolute;
    height: 30px;
    width: 30px;
    top: 10px;
    right: 20px;
}
.setting span {
    width: fit-content;
    height: fit-content;
    font-size: 17px;
    padding: 5px;
}
.setting .header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 4rem;
    background-color: #efecf2;
    width: 100%;
    padding: 1rem 0;
}
.setting .header p {
    padding: 0;
    margin: 0;
    cursor: pointer;
    position: relative;
    font-size: 40px;
}
.setting .header p::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -6px;
    width: 0%;
    height: 4px;
    background-color: rgb(246, 157, 240);
    transition: 0.3s;
}
.setting .header p:hover::after {
    width: 100%;
}