.detailes {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 80%;
    padding: 0;
    background-color: #ffff;
    margin: 2rem;
    padding: 2rem;
}
.detailes form {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 50px;
    padding: 10px;
}
.detailes form .sec {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
}
.detailes form .sec label {
    font-size: 25px;
    font-weight: 600;
    cursor: pointer;
}
.detailes form .sec.second {
    width: 40%;
}
.detailes form .sec input {
    width: 300px;
    height: 40px;
    font-size: 20px;
    font-weight: 400;
    color: #000;
    cursor: pointer;
    border-radius: 10px;
    border: 3px solid #c6c2c2;
    padding:0 ;
    text-align: center;
}
.detailes form .sec select  {
    width: 100px;
    height: 40px;
    border-radius: 10px;
    border: 3px solid #c6c2c2;
    font-size: 15px;
    color: #000;
}
@media (max-width:400px){
    .detailes form .sec {
        flex-direction: column;
        gap: 10px;
    }
    .detailes form .sec.second {
    width:0;
}
}